import React from 'react';
import { Grid, Row, Col } from 'react-bootstrap';

class About extends React.Component {
  render() {
    return (
      <Grid fluid>
        <Row className="about-row">
          {/* Render this column for screens that aren't phones */}
          <Col xs={8} xsHidden xsOffset={2}>
            <h2 className="welcome-text">Hi, I'm Derek Lause.</h2>
            <div className="about-container">
              <img title="Derek Lause" className="about-image" alt="Headshot" src={require("../../assets/images/headshot.png")} />
              <p className="about-text">
                I am a tech fanatic, software developer, constantly learning individual, and video game player.
                This website consists of written <a href='https://dlause.blogspot.com' className="about-link">content</a> of mine,
                a <a href='/portfolio' className="about-link">portfolio</a> of my current work, and a <a href='/shop' className="about-link">store</a>.
                If one would wish to contact me, please visit the <a href='/contact' className="about-link">contact page</a>.
              </p>
            </div>
          </Col>
          {/* Render this column for screens that are phones */}
          <Col xs={8} smHidden mdHidden lgHidden xsOffset={2}>
            <h2 className="welcome-text">Hi, I'm Derek Lause.</h2>
            <div className="about-container">
              <p className="about-text">
                I am a tech fanatic, software developer, constantly learning individual, and video game player.
                This website consists of written <a href='https://dlause.blogspot.com' className="about-link">content</a> of mine,
                a <a href='/portfolio' className="about-link">portfolio</a> of my current work, and a <a href='/shop' className="about-link">store</a>.
                If one would wish to contact me, please visit the <a href='/contact' className="about-link">contact page</a>.
              </p>
            </div>
          </Col>
        </Row>
      </Grid>
    );
  }
}

export default About;


